l Santuario de la Cruz Parlante, cuna de la ciudad y de la adoración a la Cruz Parlante, cuenta la leyenda que una cruz que hablaba se presentó en un árbol de cedro a un lado de un cenote sagrado, y la Cruz unió a los mayas y los guio en la lucha contra sus opresores; actualmente se puede visitar el cenote y el adoratorio, respetando las normas de los guardianes del centro ceremonial, distinción que se pasa de generación en generación. "),_c('br'),_c('br'),_vm._v(" Además del recorrido por los lugares históricos, Carrillo Puerto es famoso por su gastronomía, podrás encontrar una gran variedad de lugares donde se pueden degustar los platillos tradicionales de la zona como el Chilmole, Relleno negro, salpicón de Venado, Poc-chuc y queso relleno además de antojitos regionales como cochinita pibil, lechón, panuchos, salbutes, etc. Cuanta con una variedad de hoteles pequeños donde se puede pernoctar para continuar con la exploración de este destino poco conocido pero con mucho que ofrecer. "),_c('br'),_vm._v(" Nota: Existe un centro de medicina tradicional, holística y Sanación en las afueras de la ciudad nombrado Raxalaj Mayab, si te interesa saber más sobre las actividades vale la pena acercarte al módulo de información turística de la ciudad en la esquina de las Av. Benito Juárez con Santiago Pacheco Cruz para mayor información ")]),_c('v-img',{attrs:{"src":require("../../../../assets/alma/felipeCarrilloPuerto_i.jpg"),"max-height":"250","max-width":"350"}})],1)],1)])],1),_c('v-row',{staticClass:"mb-6",attrs:{"no-gutters":""}},[_c('v-col',{attrs:{"md":"2"}},[_c('div',{staticClass:"d-flex flex-column justify-space-between align-center"},[_c('v-img',{attrs:{"src":require("../../../../assets/alma/mapaturist.jpg"),"max-height":"270","max-width":"200"}}),_c('v-btn',{staticClass:"ma-2 white--text",attrs:{"loading":_vm.loading3,"disabled":_vm.loading3,"color":"red darken-2"},on:{"click":function($event){_vm.loader = 'loading3'}}},[_vm._v(" Descargar "),_c('v-icon',{attrs:{"ri